// Feedback states
.has-success {
//  .form-control-validation(@state-success-text; @state-success-border; @state-success-bg);
  .form-control {
    border-color: @gray-light;
    .form-control-focus();

    &:focus~.input-group-inner-addon {
      color: @brand-primary;
      background-color: #e6f5eb;
      border-color: @brand-primary;
    }
  }

  .form-item {
    .validation-icon {
      color: @brand-primary;
    }
  }

  .input-group-inner-addon {
    color: @gray-darker;
    border-color: @gray-light;
    background-color: @gray-lightist;
  }

  .form-control-feedback {
    color: @brand-primary;
  }
}

.has-warning {
    .form-control-validation(@state-warning-text; @state-warning-border; @state-warning-bg);
}

.has-error {
    .form-control-validation(@state-danger-text; @state-danger-border; @state-danger-bg);
}

