// Alerts

.alert-variant(@background; @border; @text-color) {
  background-color: @background;
  border-color: @border;
  color: @text-color;

  hr {
    border-top-color: darken(@border, 5%);
  }
  .alert-link {
    color: darken(@text-color, 10%);
    padding-left: 20px;
  }
}
.alert-warning {
  .alert-link {
    color: #5a82be !important;
  }
}